import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Recipe } from '../recipe/recipe-list/recipe.model';
import { RecipeServiceService } from '../recipe/recipe-service.service';
import { exhaustMap, map, take, tap } from 'rxjs/operators';
import { AuthService } from '../auth/auth.service';

@Injectable({
  providedIn: 'root'
})
export class DataStorageService {

  constructor(private http : HttpClient, 
              private recipeService : RecipeServiceService,
              private authService : AuthService) { }

  storeRecipes(){
    const recipes = this.recipeService.getRecipes();
    this.http.put('https://recipe-project-7242c-default-rtdb.firebaseio.com/recipes.json',recipes).subscribe(
      response => {
        console.log(response);
      }
    );
  }

  fetchRecipes(){
    return this.http.get<Recipe[]>(
      'https://recipe-project-7242c-default-rtdb.firebaseio.com/recipes.json',
    )
    .pipe(
      map(recipes => {
        return recipes.map(recipe => {
          return {...recipe, ingredients : recipe.ingredients ? recipe.ingredients : [] };
          })
        }),
        tap(recipes => {
          this.recipeService.setRecipes(recipes);
        })
      )
  }


}
